}

.user {
  font-size: 0.7em;
  margin-right: 1em;
  position: relative;
  left: -1em;
  display: inline-block;
  max-width: 3em;
  vertical-align:middle;
}

.speech-bubble1 {
	position: relative;
	/* background: #09f; */
	/* background: #188415; */
  background: #cbe5f2;
  color: black;
  padding: 0.5em;
	border-radius: .4em;
  margin-left: 1.5em;
  /* font-weight: bold; */
}

.speech-bubble1:after {
	content: '';
	position: absolute;
	right: 0;
	top: 50%;
	width: 0;
	height: 0;
	border: 24px solid transparent;
	border-left-color: #cbe5f2;
	border-right: 0;
	border-top: 0;
	margin-top: -12px;
	margin-right: -24px;
}

.speech-bubble1.result {
  background: #8ec6e2;
}

.speech-bubble1.result:after {
	border-left-color: #8ec6e2;
}

.speech-bubble2 {
  vertical-align:middle;
	position: relative;
	background: #f1f0f0;
  color: black;
  padding: 0.5em;
	border-radius: .4em;
  margin-right: 1.5em;
  display: inline-block;
}

.short-bubble {
max-width: 75%;
}

.speech-bubble2:after {
	content: '';
	position: absolute;
	left: 0;
	top: 50%;
	width: 0;
	height: 0;
	border: 24px solid transparent;
	border-right-color: #f1f0f0;
	border-left: 0;
	border-top: 0;
	margin-top: -12px;
	margin-left: -24px;
}

